@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: "Josefin Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.no-display {
  display: none !important;
}

html, body, #root {
  height: 100%;
  background: linear-gradient(217deg, rgb(50, 130, 244), rgba(255, 0, 0, 0) 70.71%), linear-gradient(127deg, rgb(184, 231, 245), rgb(33 147 137 / 71%) 70.71%), linear-gradient(336deg, rgba(25, 57, 216, 0.95), rgba(0, 0, 255, 0) 70.71%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow-x: hidden;
}

button {
  font-family: inherit;
  padding: 15px 30px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 3px 7px 0px #979797;
  border: 1px solid transparent;
  cursor: pointer;
  transition: background-color 300ms;
}

.dialog-menu {
  border: 1px solid transparent;
  box-shadow: 0px 3px 5px 1px #0000009c;
  border-radius: 5px;
  text-align: center;
  padding: 15px;
  width: 75vw;
  max-width: 400px;
  margin: 0 auto;
  background-color: #ece8da;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#root {
  display: flex;
  flex-direction: column;
}
.content {
  flex: 1 0 auto;
  position: relative;
}
.footer {
  flex-shrink: 0;
}
.game-area {
  -webkit-animation: fade-in 0.5s;
          animation: fade-in 0.5s;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.hud {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: baseline;
  width: 80vw;
  margin: 15px auto;
  max-width: 600px;
}

.timer {
  border: 2px solid white;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  color: white;
}

.time-left {
  font-size: 28px;
  text-align: center;
  font-weight: lighter;
}

.current-level {
  color: white;
}

.level, .point-counter {
  font-size: 200%;
}

.total-score {
  color: white;
}

.point-counter {
  position: relative;
}

/**/

.point-counter[animation="1"]::after {
  position: absolute;
  content: "+1";
  color: lime;
  bottom: 15px;
  left: 20px;
  text-shadow: 0px -1px 10px #46f514bd;
  font-size: 80%;
  -webkit-animation: move-up 1s;
          animation: move-up 1s;
  opacity: 0.0;
}

@-webkit-keyframes move-up {
  0% {
    opacity: 1.0;
    bottom: 20px;
    color: white;
  }
  20% {
    color: lime;
  }
  40% {
    color: rgb(182, 251, 182);
  }
  60% {
    color: rgb(48, 253, 48);
    opacity: 1.0;
  }
  80% {
    color:rgb(182, 251, 182);
  }
  100% {
    color: lime;
    opacity: 0.0;
    bottom: 40px;
  }
}

@keyframes move-up {
  0% {
    opacity: 1.0;
    bottom: 20px;
    color: white;
  }
  20% {
    color: lime;
  }
  40% {
    color: rgb(182, 251, 182);
  }
  60% {
    color: rgb(48, 253, 48);
    opacity: 1.0;
  }
  80% {
    color:rgb(182, 251, 182);
  }
  100% {
    color: lime;
    opacity: 0.0;
    bottom: 40px;
  }
}

.round-win {
  border: 1px solid transparent;
  box-shadow: 0px 3px 5px 1px #0000009c;
  border-radius: 5px;
  text-align: center;
  padding: 15px;
  width: 75vw;
  max-width: 400px;
  margin: 0 auto;
  background-color: #454544;
  -webkit-animation: appear-from-bottom 0.5s;
          animation: appear-from-bottom 0.5s;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@-webkit-keyframes appear-from-bottom {
  from {
    -webkit-transform: translate(-50%, 100%);
            transform: translate(-50%, 100%);
  }
  to {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

@keyframes appear-from-bottom {
  from {
    -webkit-transform: translate(-50%, 100%);
            transform: translate(-50%, 100%);
  }
  to {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

.advance-round-button {
  box-shadow: 1px 2px 5px 1px #2a2a2acd;;
  font-size: 125%;
}

.advance-round-button:hover {
  background-color: rgb(253, 208, 112);
}

.advance-round-button:active {
  background-color: rgb(175, 144, 76);
}

.round-win-message {
  margin: 15px 0;
  color: rgb(255, 231, 124);
  font-size: 200%;
}

.round-win-subtext {
  color: white;
  font-size: 105%;
}

.end-game-button {
  background-color: hsl(208, 100%, 40%);
  color: white;
  box-shadow: 0px 3px 5px 1px #0000006e;
}

.end-game-button:hover {
  background-color: hsl(208, 100%, 50%);
  box-shadow: 0px 3px 5px 1px #2232e9a8;
}

.confetti-canvas {
  position: fixed;
  width: 90vw;
  height: 90vh;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  pointer-events: none;
}
@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


.game-title {
  font-weight: 400;
  font-size: 225%;
  margin-block: 0;
  margin-inline: 0;
  margin: 15px 0;
}

.menu {
  width: 80vw;
  max-width: 500px;
  text-align: center;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0px 4px 11px 5px #3d3d3d6e;
  background-color: rgb(245, 245, 245);
  color: black;
  -webkit-animation: fade-in 1.0s;
          animation: fade-in 1.0s;
}

.logo {
  display: block;
  width: 15%;
  margin: 15px auto;
}

.enable-hard {
  display: block;
  cursor: pointer;
}

.play-button {
  font-size: 150%;
  background-color: hsl(208, 100%, 40%);
  color: white;
  margin-bottom: 15px;
  transition: background-color 300ms;
  cursor: pointer;
}

.play-button:hover {
  background-color: hsl(208, 100%, 60%);
}

.play-button:active {
  background-color: hsl(208, 100%, 30%);
}

.difficulty-caption {
  font-style: italic;
}

.seconds {
  color: red;
  font-weight: 700;
}

.enable-hard input[type="checkbox"] {
  position: relative;
  top: 2px;
  height: 16px;
  width: 24px;
  cursor: pointer;
}

.difficulty-caption .seconds {
  transition: color 300ms;
}

.difficulty-caption[animation="0"] .seconds {
  color: orange;
}

.difficulty-caption[animation="1"] .seconds {
  color: red;
  -webkit-animation: flashing 0.5s;
          animation: flashing 0.5s;
}

@-webkit-keyframes flashing {
  0% {
    color: red;
  }
  10% {
    color: white;
  }
  20% {
    color: rgb(255, 133, 133);
  }
  35% {
    color: white;
  }
  50% {
    color: red;
  }
  75% {
    color: white;
  }
  100% {
    color: rgb(255, 133, 133);
  }
}

@keyframes flashing {
  0% {
    color: red;
  }
  10% {
    color: white;
  }
  20% {
    color: rgb(255, 133, 133);
  }
  35% {
    color: white;
  }
  50% {
    color: red;
  }
  75% {
    color: white;
  }
  100% {
    color: rgb(255, 133, 133);
  }
}

.menu[animation="1"] {
  -webkit-animation: shrink 0.65s ease-in-out;
          animation: shrink 0.65s ease-in-out;
}

@-webkit-keyframes shrink {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
    opacity: 1.0;
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1.65);
            transform: translate(-50%, -50%) scale(1.65);
    opacity: 0.0;
  }
}

@keyframes shrink {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
    opacity: 1.0;
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1.65);
            transform: translate(-50%, -50%) scale(1.65);
    opacity: 0.0;
  }
}

.game-over {
  border: 1px solid transparent;
  box-shadow: 0px 3px 5px 1px #0000009c;
