.cards {
  min-width: 210px;
  width: 90vw;
  max-width: 800px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  flex-wrap: wrap;
}

.memory-card {
  border-radius: 5px;
  width: 200px;
  height: 200px;
  background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(229,229,229,1) 100%); 
  box-shadow: 0px 3px 5px 1px #0000006e;
  margin: 15px 5px;
  -webkit-animation: appear 0.35s;
          animation: appear 0.35s;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  cursor: pointer;
  transition: box-shadow 300ms;
}

@media (hover: hover) {
  .memory-card:hover {
    box-shadow: 0px 3px 5px 1px #ffea746e;
  }
}


.blow-up {
  -webkit-animation: blow-up 0.35s;
          animation: blow-up 0.35s;
}

@-webkit-keyframes blow-up {
  from {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    opacity: 1.0;
  }
  to {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
  }
}

@keyframes blow-up {
  from {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    opacity: 1.0;
  }
  to {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
  }
}

@-webkit-keyframes appear {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes appear {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.memory-card-image {
  min-width: 96px;
  width: 60%;
  height: 60%;
  object-fit: cover;
  max-width: 200px;
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  box-shadow: 1px 2px 5px 0px black;
}

.memory-card-label {
  overflow: hidden;
  max-width: 100%;
  text-overflow: ellipsis;
  -webkit-font-feature-settings: "pcap", "c2pc";
          font-feature-settings: "pcap", "c2pc";
  font-variant-caps: all-petite-caps;
  font-size: 150%;
  margin: 10px 5px;
  text-align: center;
  justify-self: end;
}

@media (max-width: 400px) {
  .memory-card {
    width: 128px;
  }

  .memory-card-image {
    width: 80%;
    height: 50%;
  }
}
.rotating {
  -webkit-animation: rotating infinite 2s linear;
          animation: rotating infinite 2s linear;
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading .checkmark {
  -webkit-animation: appear 0.3s ease-in;
          animation: appear 0.3s ease-in;
}

@-webkit-keyframes appear {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
  }
  80% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes appear {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
  }
  80% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

